@keyframes pisca{
    0% {opacity: 0.2;}
    25%{opacity: 0.5;}
    50% {opacity: 1;}
    75%{opacity: 0.5;}
    100% {opacity: 0.2;}
}

form{
    display: flex ;
    flex-direction: column;
    
    min-width: 300px;
    box-sizing: border-box;
    padding: 5px;
}

input,textarea{
    outline: none;
    padding: 6px;
    margin-bottom: 5px;
    font-family: Arial;
}

textarea{
    height: 80px;
}
button{
    border: none;
    height: 30px;
    background-color: rgb(32, 111, 32);
    color: white;
}
button:hover{
    background-color: rgb(22, 138, 22);
}

.pisca{
    animation-name: pisca;
    animation-duration: 3s;
    animation-iteration-count: infinite;

}

.flash{
    color: rgb(41, 78, 4);
    font-family: Arial;
    font-size: 20px;
}